import React from 'react';
import { Link } from 'react-router-dom';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import './styles.css';

const HomeSunBeachAndSand = () => {
  const listSunBeachAndSand = {
    image: "Qroo_cancun_playa108.webp",
    title: "Sol, Playa y Arena",
    subtitle: "Las mejores playas estn en Mxico",
    description: "La <b>privilegiada ubicacin de Mxico</b> le permite estar baado por el <b>Ocano Pacfico, el Golfo de Mxico y el Mar Caribe</b>, lo que hace de nuestro pas un <b>destino paradisiaco</b> para los turistas que buscan sol y playa. Las largas <b>extensiones de costa</b> que superan los <b>11 mil kilmetros</b> permiten que cada playa sea nica por s misma, con aguas de <b>distintas tonalidades y arena de diferentes texturas</b>. La flora y fauna marina tambin hacen de Mxico un imn de visitantes de todas partes del mundo. <b>El Mar de Corts</b>, considerado <b>el acuario del mundo</b>; la hermosa <b>Baha de Acapulco</b>, con uno de los climas ms privilegiados de todo el planeta; y las <b>playas oaxaqueas</b> cuyo oleaje es perfecto para la <b>prctica del surf</b>, son solo algunos ejemplos de lo que se puede disfrutar en el <b>Pacfico Mexicano</b>; mientras que del lado del Golfo de Mxico encontramos la <b>Costa Esmeralda en Veracruz</b>, y qu decir del <b>azul turquesa del Caribe Mexicano</b>, con su blanca arena.",
    button: {
      title: "Vamos",
      dir: "/sol-playa-y-arena"
    }
  };

  const makeBold = (item, keyword) => {
    var re = new RegExp(keyword, 'g')
    return (
        item.replace(re, '<b>'+keyword+ '</b>')
    )
  }
  return (
    <div className="home-sun-beach-and-sand-ctn">
      <div className="home-sun-beach-and-sand-ctn-card">
        <LazyLoadImage 
          src={require(`../../assets/img/header/${listSunBeachAndSand?.image}`)}
          alt={listSunBeachAndSand?.title}
          className="home-sun-beach-and-sand-card-img"
        />
        <div className="home-sun-beach-and-sand-ctn-card-description">
          <h4 className="home-sun-beach-and-sand-card-description-title">{listSunBeachAndSand?.title}</h4>
          <p className="home-sun-beach-and-sand-card-description-subtitle">{listSunBeachAndSand?.subtitle}</p>
          <p className="home-sun-beach-and-sand-card-description-description" 
            dangerouslySetInnerHTML={{__html: 
             listSunBeachAndSand?.description
            }} 
            
          />
          <Link 
            to={listSunBeachAndSand?.button?.dir}
            className="home-sun-beach-and-sand-card-description-button"
          >
            {listSunBeachAndSand?.button?.title}
          </Link>
        </div>
      </div>
    </div>
  );
};

export default HomeSunBeachAndSand;